<template lang="pug">
    .share-user
        .backgroundOverlay(@click="closeShare")
        .wrapper-share-user
            .header-share
                h2.title Compartilhar

            input.input-text(ref="inpuSearch", placeholder="Buscar", v-model="search")
            p.msg-errorEmail Você já adicionou esse email, tente outro!

            p.description-share Pesquise uma pessoa por nome ou insira um endereço de e-mail para convidar alguém novo.
            
            button.button-shareEmail(v-if="isShareEmail", @click="setNewUserEmail") Convidar via email.
            
            .list-searchEmail(v-if="usersEmail.length")
                .title-list Novos convidados por email
                
                .user(v-for="(user, index) in usersEmail")
                    button.button-removeUser(@click.stop="removeUserEmail(user)") Remover Seleção
                    .photo
                    .infos
                        span.name {{user.name}}
                        span.email {{user.email}}
            
            .list-usersDefault
                .title-list Compartilhado com:
                .user(v-for="(user, index) in usersDefaultShare", :class="checkSelect(user)", :data-default="checkDefault(user)", :data-email="user.email", @click.stop="selectUser(user, $event.target)")
                    button.button-removeUser(@click.stop="removeSelectUser(user, $event.target)") Remover Seleção
                    .photo(:style="{'background-image':'url('+user.infos.photo+')'}")
                    .infos
                        span.name {{user.infos.name}}
                        span.count-task 2 task

            .list-search
                .title-list Pessoas que estão no board
                .user(v-for="(user, index) in filteredList", v-if="index <= 30", :data-default="checkDefault(user)", @click.stop="selectUser(user, $event.target)")
                    button.button-removeUser(@click.stop="removeSelectUser(user, $event.target)") Remover Seleção
                    .photo(:style="{'background-image':'url('+user.infos.photo+')'}")
                    .infos
                        span.name {{user.infos.name}}
                        span.count-task 2 task

        .footer-share
            button.button-add(@click="saveUsers", v-if="isUserSelect && !isRemove") Adicionar mais {{userSelect.length}} {{userSelect.length <= 1 ? 'usuario' : 'usuarios'}} {{usersDeleteDefault.length ? 'e remover ' + usersDeleteDefault.length : ''}} {{usersDeleteDefault.length <= 1 ? 'usuario' : 'usuarios'}}
            button.button-add(@click="saveUsersDelete", v-if="isRemove") {{usersDeleteDefault.length ? 'Remover ' + usersDeleteDefault.length : ''}} {{usersDeleteDefault.length <= 1 ? 'usuario' : 'usuarios'}}
            button.button-add(@click="saveUsers", v-if="!isUserSelect && !isRemove", class="disable") Escolha alguem para compatilhar
            button.button-cancel(@click="closeShare") Cancelar

</template>

<script>
    import WeUserThumb from '@/components/common/We-UserThumb.vue'
    import Users from '@/users.js'

    export default {
        name: 'w-shareUser',
        props: ['data'],
        components:{
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                isUserSelect: false,
                isRemove: false,
                isShareEmail: false,
                tempUserEmail: null,
                usersDeleteDefault: [],
                usersEmail: [],
                search: '',
                users: Users.users,
                userSelect: [],
                tempSelectUser: [],
                usersDefaultShare: [],
                msg: 'Welcome to Your Vue.js App'
            }
        },
        computed: {
            filteredList() {
                return this.users.filter(post => {
                    // this.isUserSelect = false;
                    return post.infos.name.toLowerCase().includes(this.search.toLowerCase())
                })
            }
        },
        watch: {
            search(old, val){
                this.isShareEmail = this.validateEmail(old);
                this.tempUserEmail = old;

                for (var i = 0; i < this.usersEmail.length; i++) {
                    if (this.usersEmail[i].email == old) {
                        this.$refs.inpuSearch.classList.add('error-email');
                        this.isShareEmail = false;
                    }else{
                        this.isShareEmail = this.validateEmail(old);
                        this.$refs.inpuSearch.classList.remove('error-email');
                    }
                }
            }
        },
        mounted(){
            this.getCurrentUsersShare();
        },
        methods: {
            setNewUserEmail(){
                let fullName = this.tempUserEmail.split('@')[0].split('.');

                let tempAddUserEmail = {
                    name: fullName[0],
                    email: this.tempUserEmail
                }

                let tempUser = {
                    id: this.users.length + 1,
                    infos: {
                        photo: '',
                        name: tempAddUserEmail.name
                    },
                    email: true,
                    emailCheck: false
                }

                this.usersEmail.unshift(tempAddUserEmail);
                this.tempUserEmail = null;
                this.search = '';

                this.isUserSelect = true;
                this.userSelect.push(tempUser);
            },
            stop(){},
            validateEmail(email) {
                var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(String(email).toLowerCase());
            },
            removeUserEmail(user){
                var index = this.usersEmail.indexOf(user);
                if (index > -1) {
                    this.usersEmail.splice(index, 1);
                }
            },
            removeSelectUser(user, el){
                if (!el.parentNode.getAttribute('data-default')) {
                    var index = this.userSelect.indexOf(user);
                    el.parentNode.classList.remove('active');

                    if (index > -1) {
                        this.userSelect.splice(index, 1);
                    }

                    if (!this.userSelect.length) {
                        this.isUserSelect = false;

                        if (this.usersDeleteDefault.length) {
                            this.isRemove = true;
                        }
                    }
                    
                }else{
                    var index = this.usersDefaultShare.indexOf(user);
                    el.parentNode.classList.remove('active');

                    this.usersDeleteDefault.push(user);

                    if (!this.userSelect.length) {
                        this.isRemove = true;
                    }else{
                        this.isRemove = false;
                    }

                    // if (index > -1) {
                    //     this.usersDefaultShare.splice(index, 1);
                    // }

                    // console.log(this.usersDeleteDefault);
                }
                // let defaultUser = el.getAttribute('data-default');
                // for (var i = 0; i < this.usersDefaultShare.length; i++) {
                //     if (this.usersDefaultShare[i].id === user.id) {
                //         el.parentNode.classList.remove('active');
                //         this.usersDeleteDefault.push(this.usersDefaultShare[i]);
                //         this.isRemove = true;

                //         if (this.userSelect.length) {
                //             this.isRemove = false;
                //         }
                //     }
                // }

                // var index = this.userSelect.indexOf(user);
                // if (index > -1) {
                //     this.userSelect.splice(index, 1);
                //     el.parentNode.classList.remove('active');

                //     console.log(this.usersDefaultShare.length);

                //     if (!this.userSelect.length) {
                //         this.isUserSelect = false;
                        
                //         if (this.usersDefaultShare.length) {
                //             this.isUserSelect = false;
                //             this.isRemove = true;
                //         }
                //     }

                //     // this.isUserSelect = false;
                //     // this.isRemove = false;
                // }
            },
            saveUsersDelete(){
                for (var i = 0; i < this.data.users.length; i++) {
                    for (var j = 0; j < this.usersDeleteDefault.length; j++) {
                        if (this.usersDeleteDefault[j].id === this.data.users[i].id) {
                            this.data.users.splice(i, 1);
                        }
                    }
                }

                this.closeShare();
            },
            saveUsers(){
                if (this.data) {
                    if (this.usersDeleteDefault.length) {
                        for (var i = 0; i < this.data.users.length; i++) {
                            for (var j = 0; j < this.usersDeleteDefault.length; j++) {
                                if (this.usersDeleteDefault[j].id === this.data.users[i].id) {
                                    this.data.users.splice(i, 1);
                                }
                            }
                        }
                    }

                    for (var i = 0; i < this.userSelect.length; i++) {
                        this.data.users.push(this.userSelect[i])
                    };
                }else{
                    this.$emit('selectUsers', this.userSelect);
                }
                // this.filteredList();
                this.closeShare();
            },
            getCurrentUsersShare(){
                if (this.data) {
                    console.log(this.data.users);
                    for (var i = 0; i < this.data.users.length; i++) {
                        if (!this.data.users[i].master) {
                            this.usersDefaultShare.push(this.data.users[i])
                        }
                    }
                }
            },
            checkSelect(user){
                for (var i = 0; i < this.usersDefaultShare.length; i++) {
                    if (this.usersDefaultShare[i].id === user.id) {
                        return 'active'
                    }
                }
            },
            checkDefault(user){
                for (var i = 0; i < this.usersDefaultShare.length; i++) {
                    if (this.usersDefaultShare[i].id === user.id) {
                        return true
                    }
                }
            },
            selectUser(user, el){
                let defaultUser = el.getAttribute('data-default');
                if (!defaultUser) {
                    this.userSelect.push(user);
                    el.classList.add('active');
                    this.isUserSelect = true;

                    if (this.usersDeleteDefault.length) {
                        this.isRemove = false;
                    }
                }else{
                    var index = this.usersDeleteDefault.indexOf(user);
                    el.classList.add('active');

                    if (index > -1) {
                        this.usersDeleteDefault.splice(index, 1);
                    }

                    if (!this.usersDeleteDefault.length) {
                        this.isRemove = false;
                    }
                }

                // if (!defaultUser) {
                //     el.classList.add('active');
                //     this.userSelect.push(user);
                //     this.isUserSelect = true;
                //     // this.isRemove = false;

                //     if (!this.isUserSelect.length) {
                //         this.isRemove = false;
                //     }
                // }else{
                //     el.classList.add('active');

                //     for (var i = 0; i < this.usersDeleteDefault.length; i++) {
                //         if (this.usersDeleteDefault[i].id === user.id) {
                //             this.usersDeleteDefault.splice(i, 1);
                //         }
                //     }
                    
                //     if (!this.usersDeleteDefault.length) {
                //         this.isRemove = false;
                //     }
                // }
            },
            closeShare(){
                this.$emit('closeShare');
                this.userSelect = [];
            }
        },
        beforeDestroy(){
            // this.tempSelectUser = [];
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/ShareUser.styl"></style>


