<template lang="pug">
	.content-task
		.overlay(@click="close")
		.modal-task
			.popup-modal(ref="popup")
				.content-popup
					span.description Essa tarefa sera removida
					h3.title Tem certeza que quer excluir?

					.buttons
						button.button-confirm(@click="removeTask(currentTask)") Excluir
						button.button-cancel(@click="closePopupRemove") Cancelar Exclusão

			we-shareUser(:data="currentTask", v-if="isShareUserTask", @closeShare="closeShare")

			.group
				.header-content
					.users
						we-userThumb(:data="currentTask.users[0]", class="master")

						we-userThumb(v-for="(user, index) in currentTask.users", v-if="index > 0", :data="user", :users="currentTask.users")

						we-buttonShareUser(@openShare="openShare")

					.wrapper-options(ref="options")
						button.button-options(@click="toggleOptions") Opções
						button.button-back(@click="toggleOptions")
						
						.buttons
							button.button-hashtag Hashtags
							we-upload(class="button-arquivos", :data="currentTask.content.files")
							button.button-lembrete Lembretes
							button.button-list(@click="addList") Adicionar Lista
							button.button-checklist(@click="addCheckList") Adicionar Checklist

							.more
								button.button-more Mais Opções

								.buttons-more
									button.button-item Duplicar
									button.button-item(@click="openPopupRemove") Excluir

				.content-text
					.content-title
						we-editable(
							class="title",
							:content="currentTask.content.title",
							@update="currentTask.content.title = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar

					.content-description(v-if="currentTask.content.description")
						we-editable(
							class="description",
							:content="currentTask.content.description",
							@update="currentTask.content.description = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar

					.timeline
						span.current-date {{currentTask.date}}
						flat-pickr(class="button-date", v-model="currentDate", @on-close="closePickerDate", :config="configPicker", placeholder="Abrir calendario")

						.others
							span.date Criado as dia 21/2018 hás 8h
					
					
					we-inserts(:data="currentTask", :addComponentPre="addInserts", @checkDeleteList="resetAddInserts", @checkDeleteCheckList="resetAddInserts")

				.comments(v-if="currentTask.comments")
					.comment(
						v-for="(comment, index) in currentTask.comments", 
						:class="checkUserMaster(comment) ? 'master' : ''", 
						:key="index")
						.button-like
							.emojis
								button.button-emoji
								ul.list
									li.emoji-1
									li.emoji-2
									li.emoji-3
									li.emoji-4
									li.emoji-5

							.like Curtir

						.info-user(v-if="!checkUserMaster(comment)")
							.photo(:style="{'background-image': 'url('+getUserComment(comment.user, 'photo')+')'}")
							.name {{getUserComment(comment.user, 'name')}}
							.date-created {{comment.date}}

						.text
							.images(v-for="file in comment.files")
								img.image(:src="file.dataURL")

							p(v-html="comment.text")

			.footer
				.add-comment(@click="showComment", ref="addComment")
					we-editable(
						ref="textComment",
						:content="contentComment.text",
						@update="contentComment.text = $event", 
						placeholder="Adicionar comentario"
					)
					
					.tools
						we-upload(class="button-image", @upload="getUploads")
					
					.files-uploads(v-if="files.length")
						.header-uploads
							h3.title Arquivos anexados

						.list-files
							.file(v-for="file in files", @click="remove(file)")
								img.image(:src="file.dataURL")

								.details
									span.filename {{file.name}}
									span.size {{file.size}}

					.buttons
						button.button-add(@click="saveComment", :class="activeButtons ? 'active' : ''") Comentar
						button.button-cancel(@click.stop="closeComment") Cancelar

				button.button-concluir-task(@click="saveTaskArquivados") Concluir Tarefa
</template>

<script>
	import flatPickr from 'vue-flatpickr-component'
	import { Portuguese } from "flatpickr/dist/l10n/pt"
	import Boards from '@/components/boards/boards.js'
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeShareUser from '@/components/common/We-ShareUser.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'
	import WeButtonShareUser from '@/components/common/We-ButtonShareUser.vue'
	import MasterUser from '@/master-user.js'
	import Users from '@/users.js'
	import WeInserts from '@/components/common/We-Inserts.vue'

	import WeUpload from '@/components/tools-created/We-Upload.vue'

	import 'flatpickr/dist/flatpickr.css';

	export default {
		name: 'we-contentTask',
		components:{
			'flat-pickr': flatPickr,
			'we-inserts': WeInserts,
			'we-editable': WeEditable,
			'we-upload': WeUpload,
			'we-buttonShareUser': WeButtonShareUser,
			'we-shareUser': WeShareUser,
			'we-userThumb': WeUserThumb
		},
		data () {
			return {
				isShareUserTask: false,
				isInserts: true,
				addInserts: null,
				files: [],
				inserts: [],
				boards: Boards.boards,
				currentBoard: null,
				currentCol: null,
				activeButtons: false,
				users: Users.users,
				masterUser: MasterUser,
				myDropzone: null,
				currentTask: [],
				currentDate: null,
				usersShare: [],
				configPicker: {
					dateFormat: "d/m/Y",
					altFormat: "d M Y",
					altInput: true,
					locale: Portuguese
				},
				contentComment: {
					user: null,
					files: null,
					text: null
				},
				msg: 'Welcome to Your Vue.js App'
			}
		},
		watch:{
			'contentComment.text'(old, current){
				this.checkInputs(old);
			}
		},
		mounted(){
		},
		created(){
			this.getBoard(this.$route.params.idBoard);
			this.getTask();
			this.setDateDafult();
		},
		methods:{
			closePickerDate(data){
				let today = new Date(data);
				let dd = today.getDate();
				let mm = today.getMonth()+1; //January is 0!
				let yyyy = today.getFullYear();

				this.currentDate = dd+'/'+mm+'/'+yyyy;
				this.currentTask.date = this.currentDate;
			},
			setDateDafult(){
				this.currentDate = this.currentTask.date;
			},
			saveTaskArquivados(){
				var index = this.currentCol.tasks.indexOf(this.currentTask);
				if (index > -1) {
					this.currentCol.tasks.splice(index, 1);
				}

				this.currentBoard.arquivados.push(this.currentTask);

				this.close();
			},
			resetAddInserts(){
				this.addInserts = null;
			},
			openShare(){
				this.isShareUserTask = true;
			},
			closeShare(){
				this.isShareUserTask = false;
			},
			addList(){
				let templist = {
					title: null,
					items: []
				}

				this.currentTask.content.list.push(templist);
			},
			addCheckList(){
				let tempchecklist = {
					title: null,
					items: []
				}

				this.currentTask.content.checklist.push(tempchecklist);
			},
			getUploadsTask(files){
				if (!this.currentTask.content.files.length) {
					this.currentTask.content.files = files;
				}else{
					for (var i = 0; i < files.length; i++) {
						this.currentTask.content.files.push(files[i]);
					}
				}
			},
			close(){
				this.$router.go(-1);
				this.saveTask();
			},
			openPopupRemove(){
				this.$refs.popup.classList.add('show');
			},
			closePopupRemove(){
				this.$refs.popup.classList.remove('show');
			},
			removeTask(task){
				var index = this.currentCol.tasks.indexOf(task);
				if (index > -1) {
					this.currentCol.tasks.splice(index, 1);
				}

				this.close();
			},
			editorTask(tollType){
				let temp = {
					name: 'we-'+tollType
				}

				this.inserts.push(temp);
			},
			getUploads(files){
				this.files = files;
			},
			saveTask(comment){
				for (var i = 0; i < this.usersShare.length; i++) {
					this.currentTask.users.push(this.usersShare[i])
				}
			},
			clearInputComment(){
				let temp = {
					user: null,
					files: null,
					text: ''
				};

				this.files = [];
				this.activeButtons = false;
				this.contentComment = temp;
				this.$refs.textComment.$el.innerHTML = '';
			},
			checkInputs(old){
				if (old != '') {
					this.activeButtons = true;
				}else{
					this.activeButtons = false;
				}
			},
			getUserComment(id, type){
				for (var i = 0; i < this.users.length; i++) {
					if (this.users[i].id === id) {
						return this.users[i].infos[type];
					}
				}
			},
			showComment(){
				this.$refs.addComment.classList.add('show');
			},
			closeComment(){
				this.$refs.addComment.classList.remove('show');
				this.clearInputComment();
			},
			checkUserMaster(comment){
				if (this.masterUser.id === comment.user) {
					return true
				}
			},
			saveComment(){
				let tempComment = {
					user: this.masterUser.id,
					text: this.contentComment.text,
					files: this.files
				}

				this.currentTask.comments.push(tempComment);
				this.clearInputComment();
			},
			getBoard(id){
				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == id) {
						this.currentBoard = this.boards[i];
					}
				}
			},
			getTask(){
				let idCol = this.$route.params.idCol;
				let idTask = this.$route.params.idTask;

				
				for (var i = 0; i < this.currentBoard.cols.length; i++) {
					if (this.currentBoard.cols[i].id === Number(idCol)) {
						this.currentCol = this.currentBoard.cols[i];
					}
				}

				for (var i = 0; i < this.currentCol.tasks.length; i++) {
					if (this.currentCol.tasks[i].id === Number(idTask)) {
						this.currentTask = this.currentCol.tasks[i];
					}
				}
			},
			toggleOptions(){
				this.$refs.options.classList.toggle('show-options');
			}
		}
	}
</script>
<style lang="stylus" src="@/assets/styles/common/modais/ContentTask.styl"></style>


